.home {
  .homeContent {
    background: linear-gradient(115deg, $color-2 50%, transparent 100%);
    min-height: $min-height;
    width: 100%;
    min-height: 500px;
    border-radius: $borderRadius;
    position: relative;
    overflow: hidden;

    &:after {
      content: "";
      position: absolute;
      height: 100%;
      width: inherit;
      background: url("../../media/aliprofil2.jpg") no-repeat center/cover;
      z-index: -1;
      filter: brightness(70%);
    }

    .content {
      position: absolute;
      top: 20%;
      left: 13%;
      font-family: $font-2;

      h1 {
        font-size: 5rem;
        font-family: $font-1;
        color: $color-1;
        text-transform: uppercase;
        text-shadow: 3px 2px 2px $color-2;
      }
      h2 {
        font-size: 2.5rem;
        padding: 1.5rem 0 7rem;
        color: $color-1;
        text-shadow: 1px 2px 1px $color-2;
      }
      //   .pdf {
      //     a {
      //       font-size: 1.5rem;
      //       background: $color-3;
      //       padding: 1rem 2rem;
      //       border-radius: $borderRadius;
      //       box-shadow: 0 5px 1px rgb(15, 15, 15);
      //       transition: 0.2s ease;

      //       &:hover {
      //         background: $color-4;
      //       }
      //     }
      //   }
    }
  }
}
@media screen and (max-width: 850px) {
  .homeContent {
    margin: 0 !important;
  }
}
@media screen and (max-width: 580px) {
  .homeContent {
    &:after {
      background: url("../../media/aliprofil2.jpg") no-repeat 60% 50% / cover !important;
    }
    h1 {
      font-size: 3rem !important;
    }
    h2 {
      font-size: 1.8rem !important;
    }
  }
}
